function getRandomString(length = 25) {
    var randomChars =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var result = "";
    for (var i = 0; i < length; i++) {
        result += randomChars.charAt(
            Math.floor(Math.random() * randomChars.length)
        );
    }
    return "0X" + result;
}

function createData(txhash, block, from, deposited, received, txfee) {
    return {txhash, block, from, deposited, received, txfee};
}

export async function addDataIntervals(arr, setArr, behavior) {
    let arrCopy = [...arr];
    let position = false;
    let from = getRandomString();
    let decimalNumbers = 3;
    const {intervalTime, minRows} = behavior;
    const calcInterval = () => {
        return (
            Math.random() *
                (intervalTime.maxValue * 1000 - intervalTime.minValue * 1000) +
            intervalTime.minValue * 1000
        );
    };

    // const maxValue = 150;
    const calcValue = () => {
        const {minValue, maxValue} = behavior.currency;
        // switch (behavior.currency) {
        //     case "eth":
        //         return Math.random() * (maxValue - minValue) + minValue;
        //     case "btc":
        //         return Math.random() * (maxValue - minValue) + minValue;
        //     default:
        //         return Math.random() * (maxValue - minValue) + minValue;
        // }
        return Math.random() * (maxValue - minValue) + minValue;
    };

    let received = calcValue().toFixed(decimalNumbers);
    let deposited = (received * 2).toFixed(decimalNumbers);

    const calcFee = () => {
        // const {code} = behavior.currency;
        switch (behavior.currency.code) {
            case "eth":
                return deposited * 0.000672;
            case "btc":
                return deposited * 0.00012;
            default:
                return deposited * 0.000672;
        }
        // return deposited * 0.000672;
    };
    let txFee = calcFee().toFixed(8);

    for (let i = 0; i < behavior.minRows; i++) {
        arrCopy = [
            ...arrCopy,
            createData(
                getRandomString(),
                8456553,
                from,
                deposited,
                received,
                txFee
            ),
        ];
        setArr(arrCopy);
        from = getRandomString();
        received = calcValue().toFixed(decimalNumbers);
        deposited = (received * 2).toFixed(decimalNumbers);
        txFee = calcFee().toFixed(8);
    }

    const interval = setInterval(function () {
        position = !position;
        arrCopy = [
            ...arrCopy,
            //txhash, block, from, deposited, received, txfee
            createData(
                getRandomString(),
                8456553,
                from,
                deposited,
                received,
                txFee
            ),
        ];
        setArr(arrCopy);
        from = getRandomString();
        received = calcValue().toFixed(decimalNumbers);
        deposited = (received * 2).toFixed(decimalNumbers);
        txFee = calcFee().toFixed(8);
    }, calcInterval());
    return interval;
}
