import React, {useEffect, useState} from "react";
import "./App.css";
import MaterialTableDemo from "./Table/index";
import VirtualizedTable from "./VirtualizedTable/index";
import {createMuiTheme, MuiThemeProvider} from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import {loadJSON} from "./tableUtils/loadJSON";

function App() {
    // const theme = createMuiTheme(styles);
    const [theme, setTheme] = useState({});
    const [isLoading, setLoading] = useState(true);

    useEffect(() => {
        loadJSON("theme.json").then((data) => {
            setTheme(createMuiTheme(data));
            setLoading(false);
        });
        return () => {};
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return !isLoading ? (
        <MuiThemeProvider theme={theme}>
            <CssBaseline />
            {/* <MaterialTableDemo /> */}
            <VirtualizedTable />
        </MuiThemeProvider>
    ) : null;
}

export default App;
