class MuiVirtualizedTable extends React.PureComponent {
    static defaultProps = {
        headerHeight: 53,
        rowHeight: 53,
        endRef: React.createRef(),
    };

    // state = {
    //     headerHeight: 48,
    //     rowHeight: 48,
    // };
    componentDidMount() {
        if (this.props.style) {
            console.log(this.props);
            // this.props.style.row.height
            // state.headerHeight = this.prop.style.row.height;
            // state.rowHeight = this.prop.style.row.height;
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.rowCount > prevProps.rowCount) {
            this.props.endRef.current.scrollToRow(this.props.rowCount);
        }
    }

    getRowClassName = ({index}) => {
        const {classes, onRowClick} = this.props;

        return clsx(classes.tableRow, classes.flexContainer, {
            [classes.tableRowHover]: index !== -1 && onRowClick != null,
        });
    };

    cellRenderer = ({cellData, columnIndex}) => {
        const {columns, classes, rowHeight, onRowClick} = this.props;
        const extraClass =
            columns[columnIndex].dataKey === "txfee" ? classes.txfee : null;
        return (
            <Fade in={true} style={{transitionDelay: "75ms"}}>
                <TableCell
                    component="div"
                    className={clsx(
                        classes.tableCell,
                        classes.flexContainer,
                        {
                            [classes.noClick]: onRowClick == null,
                        },
                        extraClass
                    )}
                    variant="body"
                    style={{height: rowHeight}}
                    align={
                        (columnIndex != null && columns[columnIndex].numeric) ||
                        false
                            ? "right"
                            : "left"
                    }
                >
                    {cellData}
                </TableCell>
            </Fade>
        );
    };

    headerRenderer = ({label, columnIndex}) => {
        const {headerHeight, columns, classes} = this.props;
        const extraClass =
            columns[columnIndex].dataKey === "txfee" ? classes.txfee : null;

        // const
        // console.log(totalWidth);
        return (
            <TableCell
                component="div"
                className={clsx(
                    classes.tableCell,
                    classes.flexContainer,
                    classes.noClick,
                    classes.head,
                    extraClass
                )}
                variant="head"
                style={{
                    height: headerHeight,
                    width: columns[columnIndex].width,
                }}
                align={columns[columnIndex].numeric || false ? "right" : "left"}
            >
                <span>{label}</span>
            </TableCell>
        );
    };

    render() {
        const {
            classes,
            columns,
            rowHeight,
            headerHeight,
            ...tableProps
        } = this.props;
        return (
            <AutoSizer>
                {({height, width}) => (
                    <Table
                        height={height}
                        width={width}
                        rowHeight={rowHeight}
                        gridStyle={{
                            direction: "inherit",
                            overflowY: "hidden",
                        }}
                        headerHeight={headerHeight}
                        className={classes.table}
                        {...tableProps}
                        rowClassName={this.getRowClassName}
                        ref={this.props.endRef}
                    >
                        {columns.map(({dataKey, ...other}, index) => {
                            return (
                                <Column
                                    key={dataKey}
                                    headerRenderer={(headerProps) =>
                                        this.headerRenderer({
                                            ...headerProps,
                                            columnIndex: index,
                                        })
                                    }
                                    className={classes.flexContainer}
                                    cellRenderer={this.cellRenderer}
                                    dataKey={dataKey}
                                    {...other}
                                />
                            );
                        })}
                    </Table>
                )}
            </AutoSizer>
        );
    }
}

MuiVirtualizedTable.propTypes = {
    classes: PropTypes.object.isRequired,
    columns: PropTypes.arrayOf(
        PropTypes.shape({
            dataKey: PropTypes.string.isRequired,
            label: PropTypes.string.isRequired,
            numeric: PropTypes.bool,
            width: PropTypes.number.isRequired,
        })
    ).isRequired,
    headerHeight: PropTypes.number,
    onRowClick: PropTypes.func,
    rowHeight: PropTypes.number,
};

// const VirtualizedTable = withStyles(styles)(MuiVirtualizedTable);
const VirtualizedTable = MuiVirtualizedTable;

export default function ReactVirtualizedTable() {
    const [rows, setRows] = useState([]);
    const [columns, setColumns] = useState([]);
    const [style, setStyle] = useState({});
    const listRef = useRef();
    // const [columns, setColumns] = useState();
    const classes = useStyles(style);
    const isLoading = useLoadTable({
        rows,
        setRows,
        setStyle,
        intervalTime: style.behavior,
        setColumns,
    });
    useEffect(() => {
        // onResize();
    }, [style, isLoading]);

    // "txhashWidth": 250
    return !isLoading ? (
        <div
            style={{
                height: style.maxHeight,
                width: "100%",
                minWidth: style.totalWidth,
            }}
        >
            <VirtualizedTable
                rowCount={rows.length}
                rowGetter={({index}) => rows[index]}
                classes={classes}
                style={style}
                columns={columns}
            />
        </div>
    ) : null;
}
