import {useEffect, useState} from "react";
import {addDataIntervals} from "./interval";
import {loadJSON} from "./loadJSON";

export default function useLoadTable({
    rows,
    setRows,
    setStyle,
    setColumns,
    listRef,
}) {
    const [isLoading, setLoading] = useState(true);
    useEffect(() => {
        let interval = null;
        //make it so setColumns is bigger with more
        loadJSON("table.json").then((data) => {
            interval = addDataIntervals(rows, setRows, {...data.behavior});
            const {
                depositedWidth,
                fromWidth,
                receivedWidth,
                blockWidth,
                txfeeWidth,
                txhashWidth,
            } = data;
            const totalWidth =
                depositedWidth +
                fromWidth +
                receivedWidth +
                blockWidth +
                txfeeWidth +
                txhashWidth;
            console.log();
            let auxArr = [];
            if (window.outerWidth > totalWidth) {
                auxArr = [
                    {
                        width: data.txhashWidth ? data.txhashWidth : 0,
                        label: "TXHASH",
                        dataKey: "txhash",
                        numeric: false,
                    },
                    {
                        width: data.blockWidth ? data.blockWidth : 0,
                        label: "BLOCK",
                        dataKey: "block",
                        numeric: true,
                    },
                    {
                        width: data.txfeeWidth ? data.txfeeWidth : 0,
                        label: "[TXFEE]",
                        dataKey: "txfee",
                        numeric: true,
                    },
                ];
            }

            setColumns([
                {
                    width: data.fromWidth ? data.fromWidth : 0,
                    label: "FROM",
                    dataKey: "from",
                    numeric: false,
                },
                {
                    width: data.receivedWidth ? data.receivedWidth : 0,
                    label: "Received",
                    dataKey: "received",
                    numeric: true,
                },
                {
                    width: data.depositedWidth ? data.depositedWidth : 0,
                    label: "Deposited",
                    dataKey: "deposited",
                    numeric: true,
                },
                ...auxArr,
            ]);
            setStyle({
                ...data,
                totalWidth,
                maxHeight:
                    data.row.height * (data.behavior.rows + 1) +
                    (data.behavior.offset ? data.behavior.offset : 0),
                container: {
                    ...data.container,
                },
            });
        });
        setLoading(false);
        return () => {
            clearInterval(interval);
            console.log("clean");
        };

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    return isLoading;
}
