import React, {useState, useEffect, useRef} from "react";
import {makeStyles} from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Slide from "@material-ui/core/Slide";
import useLoadTable from "./../tableUtils/useLoadTable";

const useStyles = makeStyles({
    root: (props) => ({
        ...props.root,
    }),
    head: (props) => ({
        ...props.head,
    }),
    table: (props) => ({
        ...props.table,
    }),
    body: (props) => ({
        ...props.body,
    }),
    txfee: (props) => ({
        ...props.txfee,
    }),
    row: (props) => ({
        ...props.row,
    }),
    container: (props) => ({
        ...props.container,
    }),
});

export default function SimpleTable() {
    const [style, setStyle] = useState({});
    const classes = useStyles(style);
    const [rows, setRows] = useState([]);
    const endRef = useRef();
    useLoadTable({rows, setRows, setStyle});

    // useEffect(() => {
    //     const interval = addDataIntervals(rows, setRows, 6);
    //     loadJSON("table.json").then((data) => {
    //         setStyle({
    //             ...data,
    //             container: {
    //                 ...data.container,
    //                 maxHeight:
    //                     (data.behavior.rows + 1) * data.row.height +
    //                     data.behavior.offset,
    //             },
    //         });
    //     });
    //     return () => {
    //         clearInterval(interval);
    //         console.log("clean");
    //     };

    //     // eslint-disable-next-line react-hooks/exhaustive-deps
    // }, []);
    const scrollToBottom = () => {
        // endRef.current.scrollIntoView({behavior: "smooth"});
        endRef.current.scrollTop = endRef.current.scrollHeight;
    };
    useEffect(() => {
        scrollToBottom();
        // console.log(style);
    }, [rows]);

    return (
        <div className={classes.root}>
            <TableContainer
                component={Paper}
                className={classes.container}
                ref={endRef}
            >
                <Table
                    stickyHeader
                    className={classes.table}
                    aria-label="sticky table"
                >
                    <TableHead
                        classes={{
                            root: classes.head,
                        }}
                    >
                        <TableRow>
                            <TableCell>TXHASH</TableCell>
                            <TableCell align="right">BLOCK</TableCell>
                            <TableCell align="right">FROM</TableCell>
                            <TableCell align="right">RECEIVED</TableCell>
                            <TableCell align="right">DEPOSITED</TableCell>
                            <TableCell
                                align="right"
                                className={classes.txfee}
                                style={{fontSize: "8px"}}
                            >
                                [TXFEE]
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody
                        classes={{
                            root: classes.body,
                        }}
                    >
                        {rows.map((row, index) => (
                            <Slide
                                direction="down"
                                in={true}
                                mountOnEnter
                                unmountOnExit
                                key={"tx-" + index}
                            >
                                <TableRow
                                    classes={{
                                        root: classes.row,
                                    }}
                                >
                                    <TableCell
                                        className={classes.txhash}
                                        component="th"
                                        scope="row"
                                    >
                                        {row.txhash}
                                    </TableCell>
                                    <TableCell
                                        className={classes.block}
                                        align="right"
                                    >
                                        {row.block}
                                    </TableCell>
                                    <TableCell
                                        className={classes.from}
                                        align="right"
                                    >
                                        {row.from}
                                    </TableCell>
                                    <TableCell
                                        align="right"
                                        className={classes.received}
                                    >
                                        {row.received.toFixed(3)}
                                    </TableCell>

                                    <TableCell
                                        className={classes.deposited}
                                        align="right"
                                    >
                                        {row.deposited.toFixed(3)}
                                    </TableCell>

                                    <TableCell
                                        className={classes.txfee}
                                        align="right"
                                    >
                                        {row.txfee.toFixed(5)}
                                    </TableCell>
                                </TableRow>
                            </Slide>
                        ))}
                    </TableBody>
                </Table>
                {/* <div style={{float: "left", clear: "both"}} ></div> */}
            </TableContainer>
        </div>
    );
}
